  text-align: center; }

div.calendar td,
div.calendar th {
  text-align: center;
  vertical-align: middle;
  padding: 0.5rem 1rem 0.5rem 0; }

div.calendar td.title {
  font-weight: bold;
  color: #000; }

div.calendar th {
  text-align: right;
  font-weight: bold; }

div.calendar tr.days td {
  width: 14%;
  color: #000;
  text-align: right;
  vertical-align: middle;
  cursor: pointer;
  border: 1px solid #fff; }

div.calendar tr.days td:hover,
div.calendar td.button:hover {
  border-color: #ff7e00;
  background-color: #e5e5e5;
  cursor: pointer; }

div.calendar tr.days td:hover {
  background-color: #fff; }

div.calendar tr.days td:active
div.calendar td.button:active {
  background-color: #cde; }

div.calendar tr.days td.selected {
  font-weight: bold;
  background-color: #fbf7b2;
  border-color: #fbf7b2 !important;
  color: #000 !important; }

th.weekend {
  color: #e85000; }

div.calendar tr.days td.weekend {
  color: #000; }

div.calendar tr.days td.today {
  border-color: #ccc; }

div.calendar tr.days td.otherDay {
  background: #e5e5e5;
  color: #999;
  border: 1px solid #e5e5e5; }

/* Loader */
.block-ui {
  display: none; }

.block-ui .wrapper {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  text-align: center;
  vertical-align: middle; }

.block-ui .cssload-loader {
  position: absolute;
  display: inline-block;
  z-index: 1000;
  top: 40%;
  left: 0;
  right: 0;
  margin: auto;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background-color: #357; }

.block-ui .cssload-loader,
.block-ui .cssload-loader:before,
.block-ui .cssload-loader:after {
  animation: 0.5s infinite ease-in-out;
  -o-animation: 0.5s infinite ease-in-out;
  -ms-animation: 0.5s infinite ease-in-out;
  -webkit-animation: 0.5s infinite ease-in-out;
  -moz-animation: 0.5s infinite ease-in-out; }

.block-ui .cssload-loader:before,
.block-ui .cssload-loader:after {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 0; }

.block-ui .cssload-loader {
  animation-name: cssload-loader;
  -o-animation-name: cssload-loader;
  -ms-animation-name: cssload-loader;
  -webkit-animation-name: cssload-loader;
  -moz-animation-name: cssload-loader; }

@keyframes cssload-loader {
  from {
    transform: scale(0);
    opacity: 1; }
  to {
    transform: scale(1);
    opacity: 0; } }

@-o-keyframes cssload-loader {
  from {
    -o-transform: scale(0);
    opacity: 1; }
  to {
    -o-transform: scale(1);
    opacity: 0; } }

@-ms-keyframes cssload-loader {
  from {
    -ms-transform: scale(0);
    opacity: 1; }
  to {
    -ms-transform: scale(1);
    opacity: 0; } }

@-webkit-keyframes cssload-loader {
  from {
    -webkit-transform: scale(0);
    opacity: 1; }
  to {
    -webkit-transform: scale(1);
    opacity: 0; } }

@-moz-keyframes cssload-loader {
  from {
    -moz-transform: scale(0);
    opacity: 1; }
  to {
    -moz-transform: scale(1);
    opacity: 0; } }

/* File */
form .loadfile {
  margin-top: 0.2rem; }

form .loadfile .file {
  display: inline-block;
  min-width: 6rem;
  max-width: 50%;
  height: 3rem;
  line-height: 3rem;
  overflow-x: hidden;
  border: 1px solid #999;
  border-radius: 2px;
  vertical-align: middle;
  text-align: center;
  padding: 0 1rem;
  cursor: pointer; }

.file-hidden {
  height: 0;
  width: 0;
  overflow: hidden; }

/* Autocomplete */
.autocomplete-container {
  position: relative; }

.autocomplete-suggestions {
  margin-top: 0.5rem;
  border: 1px solid #999;
  border-radius: 2px;
  background-color: #ffffff;
  box-shadow: 0 5px 11px 0 rgba(0, 0, 0, 0.18), 0 4px 15px 0 rgba(0, 0, 0, 0.15);
  border: none;
  border-collapse: separate;
  border-spacing: 1px;
  background-color: #fff;
  overflow: auto;
  font-size: 1rem; }

.autocomplete-suggestion {
  padding: 0.5rem 1rem;
  white-space: nowrap;
  overflow: hidden;
  cursor: pointer; }

.autocomplete-selected {
  background-color: #f0f0f0; }

.autocomplete-suggestions strong {
  font-weight: normal;
  color: red; }

.autocomplete-group {
  padding: 0.25rem 0.5rem; }

.autocomplete-group strong {
  display: block;
  border-bottom: 1px solid #000; }

/* Other */
.right {
  float: right; }

span.error {
  display: none;
  margin-left: 0.5rem;
  border: none;
  border-radius: 2px;
  padding: 1px 0.5rem;
  background-color: orangered;
  color: white; }

.red {
  color: orangered; }

/* Calendar */
.vdp-datepicker {
  display: inline-block; }

.vdp-datepicker .today {
  background-color: #ccc; }

.vdp-datepicker__calendar {
  box-shadow: 0 3px 7px 0 rgba(0, 0, 0, 0.18), 0 2px 11px 0 rgba(0, 0, 0, 0.15);
  border: none;
  white-space: normal; }



