
nav {
    position: fixed;
    display: block;
    z-index: 100;
    top: 0;
    left: 0;
    right: 0;
    height: 5rem;
    line-height: 5rem;
    border-bottom: 1px solid #ddd;
    background-color: white;
    color: black;
}
nav div.container {
    z-index: 110;
    height: 5rem;
}
nav .logo {
    display: block;
    position: relative;
    float: left;
    margin-left: 2rem;
    color: inherit;
    font-size: 1.5rem;
}
nav .logo a {
    font-size: 1.5rem;
    text-decoration: none;
    color: inherit;
}
nav ul.switch {
    position: absolute;
    left: 20%;
    margin-left: 2rem;
}
nav ul.switch > li {
    float: left;
    margin-right: 3rem;
    height: 5rem;
    line-height: 5rem;
    vertical-align: middle;
    padding: 0;
    font-size: 1.2rem;
    cursor: pointer;
}
nav ul.switch > li a {
    color: inherit;
    text-decoration: none;
    padding: 0;
}
nav ul.switch > li.active {
    border-bottom: 3px solid orangered;
}
nav ul.right {
    float: right;
}
nav ul.right > li {
    float: left;
    margin-left: 3rem;
    height: 5rem;
    line-height: 5rem;
    vertical-align: middle;
    font-size: 1.2rem;
    padding: 0;
}
nav ul.right > li a {
    color: inherit;
    text-decoration: none;
    padding: 0;
}
nav .avatar {
    position: relative;
    top: 0.5rem;
    height: 4rem;
    line-height: 4rem;
    margin: 0 1rem 0 1rem;
    text-align: center;
    vertical-align: middle;
    overflow: hidden;
    cursor: pointer;
}
nav .avatar img {
    width: 4rem;
    height: 4rem;
    border: none;
    border-radius: 2px;
    background-color: #ddd;
}
nav .avatar i.fa {
    position: relative;
    top: -1.3rem;
    margin-left: 1rem;
    font-size: 1.5rem;
    color: inherit;
}
nav .menu {
    display: block;
    position: absolute;
    z-index: 100;
    top: 5rem;
    right: 0;
    margin: 0;
    padding: 0;
    box-shadow: 0 3px 7px 0 rgba(0, 0, 0, 0.18), 0 2px 11px 0 rgba(0, 0, 0, 0.15);
    border: none;
    border-collapse: separate;
    border-spacing: 1px;
    background-color: white;
    font-size: 1rem;
}
nav .menu ul {
    margin: 0;
    padding: 0;
}
nav .menu ul > li {
    float: none;
    height: 3rem;
    line-height: 3rem;
    margin: 0;
    padding: 0;
    white-space: nowrap;
    color: black;
}
nav .menu ul > li:hover {
    background-color: #eee;
    transition: 0.2s;
}
nav .menu ul > li.title {
    height: auto;
    line-height: 1.5rem;
    padding: 1rem 3rem 0.5rem 2rem;
    background-color: white;
    color: #357;
}
nav .menu ul > li.divider {
    height: 1px;
    background-color: #ccc;
    padding: 0;
}
nav .menu ul > li a {
    display: block;
    padding: 0 3rem 0 2rem;
    text-decoration: none;
    color: black;
}
nav .menu ul > li span {
    padding: 0 3rem 0 2rem;
    text-decoration: none;
    color: black;
    cursor: pointer;
}
.main > div.container::-webkit-scrollbar,
.sidebar::-webkit-scrollbar {
    width: 10px;
    background-color: rgba(127, 127, 127, 0.2);
}
.main > div.container::-webkit-scrollbar-thumb,
.sidebar::-webkit-scrollbar-thumb {
    background-color: rgba(127, 127, 127, 0.5);
}
.sidebar {
    position: absolute;
    z-index: 120;
    top: 5rem;
    left: 0;
    bottom: 0;
    width: 20%;
    background-color: white;
    color: black;
    overflow-y: auto;
    overflow-x: hidden;
    margin: 2rem 0;
    padding: 0;
}
.sidebar div.container {
    position: absolute;
    z-index: 130;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    padding: 0 2rem;
}
.sidebar a {
    color: inherit;
}
.sidebar small {
    color: #234e9e;
}
.sidebar div[mode] {
    display: none;
}
.sidebar ul.menu {
    margin: 0;
}
.sidebar ul.menu > li {
    margin: 0;
    padding: 0.5rem 0 1rem 0;
}
.sidebar ul.menu > li a {
    text-decoration: underline;
    color: inherit;
}
.sidebar ul.menu > li span {
    text-decoration: underline;
    color: inherit;
    cursor: pointer;
}
.sidebar ul.menu > li .router-link-active {
    color: teal;
}
.sidebar ul.menu > li i.fa {
  margin-right: 0.5rem;
  width: 1.5rem;
  text-align: left;
  color: #357;
}
.sidebar input[type="text"] {
    margin: 0 0 2rem 0;
    width: 10rem;
    border: 1px solid #999;
    color: black;
}
.sidebar input[type="text"]:focus:not([readonly]) {
    border: 1px solid #999;
    background-color: white;
}
.sidebar ::-webkit-input-placeholder {
    color: #666;
}
.sidebar :-moz-placeholder {
    color: #666;
}
.sidebar ::-moz-placeholder {
    color: #666;
}
.sidebar :-ms-input-placeholder {
    color: #666;
}
.sidebar ul.items > li {
    margin: 0;
    padding: 0 0 1rem 0;
}
.sidebar ul.items > li a.router-link-active {
    color: teal;
}
.sidebar ul.items > li .total {
    position: relative;
    display: inline-block;
    top: 1px;
    margin-left: 1rem;
    height: 1.5rem;
    line-height: 1.5rem;
    padding: 0 0.5rem;
    background-color: tan;
    color: black;
    font-weight: normal;
    border-radius: 2px;
}
.sidebar .block-elements {
    margin: 0 0 1rem 0;
}
.sidebar .block-elements h2 {
    margin: 0 0 0.5rem 0;
    color: #37a;
    font-size: 1.3rem;
    font-weight: normal;
}
.sidebar .block-elements ul > li {
    padding: 0 0 0.5rem 0.25rem;
}
.sidebar .block-elements ul > li a.router-link-active {
    color: teal;
}
.main {
    position: absolute;
    top: 5rem;
    left: 20%;
    bottom: 0;
    width: 80%;
    padding: 0;
}
.main > .container {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow-y: scroll;
    padding: 2rem 2rem;
    background-color: white;
}
.main div.leaf {
    display: table;
    margin-bottom: 3rem;
}
.main .path {
    display: table;
    margin: 0 0 1rem 0;
    color: black;
    font-size: 1.5rem;
    line-height: 1.5rem;
}
.main .path b {
  font-weight: normal;
  color: steelblue;
}
.main .path a {
    display: block;
    float: left;
    text-decoration: none;
    border-bottom: 1px solid #37a;
    color: #357;
}
.main .path span {
    display: block;
    float: left;
    text-decoration: none;
    border-bottom: 1px solid transparent;
}
.main .path .divider {
    display: block;
    float: left;
    margin: 0 1rem;
    color: silver;
}

/* Tree */
.tree {
  margin-bottom: 3rem;
}
.tree .margin {
  margin: 5px 0 10px 0;
  white-space: nowrap;
}
.tree .item {
  margin: 0.5rem 0 0 0;
  padding-left: 1.5rem;
  font-size: 0.8rem;
  color: #234e9e;
  font-weight: bold;
}
.tree > div > div[item] > .item {
  margin: 0;
  padding-left: 0;
  font-size: 1.3rem;
  color: #37a;
  font-weight: normal;
}
.tree .plus {
  width: 1rem;
  height: 1rem;
  float: left;
  margin: 0 0.5rem 0 0;
  cursor: pointer;
  cursor: hand;
}
.tree .minus {
  width: 1rem;
  height: 1rem;
  float: left;
  margin: 0 0.5rem 0 0;
  cursor: pointer;
  cursor: hand;
}
.tree .fa {
  position: relative;
  top: -2px;
  color: #3a7;
  font-size: 1.5rem;
}
.tree .empty {
  width: 1rem;
  height: 1rem;
  float: left;
  margin: 1px 0.5rem 0 0;
}
.tree a.router-link-active {
  color: teal;
}
.tree .padding {
  margin-left: 0.5rem;
  padding-left: 1rem;
  overflow: hidden;
}

/* Context menu */
.context {
    display: block;
    position: absolute;
    z-index: 120;
    top: -9999px;
    left: -9999px;
    margin: 0;
    margin-bottom: 2rem;
    padding: 0;
    box-shadow: 0 3px 7px 0 rgba(0, 0, 0, 0.18), 0 2px 11px 0 rgba(0, 0, 0, 0.15);
    border: none;
    border-collapse: separate;
    border-spacing: 1px;
    background-color: white;
    font-size: 1rem;
}
.context ul {
    margin: 0;
    padding: 0;
}
.context ul > li {
    float: none;
    height: 3rem;
    line-height: 3rem;
    margin: 0;
    padding: 0;
    white-space: nowrap;
    color: black;
}
.context ul > li:hover {
    background-color: #eee;
    transition: 0.2s;
}
.context ul > li.title {
    height: auto;
    line-height: 1.5rem;
    padding: 1rem 3rem 0.5rem 2rem;
    background-color: white;
    color: #357;
}
.context ul > li.divider {
    height: 1px;
    background-color: #ccc;
    padding: 0;
}
.context ul > li a {
    display: block;
    padding: 0 3rem 0 2rem;
    text-decoration: none;
    color: black;
}
.context ul > li span {
    padding: 0 3rem 0 2rem;
    text-decoration: none;
    color: black;
    cursor: pointer;
}
.context ul > li .fa {
  margin-right: 0.5rem;
  font-size: 0.8rem;
}

/* Transitions */
.fade-enter-active,
.fade-leave-active {
  transition: opacity .2s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.fade-enter-to,
.fade-leave {
  opacity: 1;
}
.switch-enter-active {
  transition: opacity .8s;
}
.switch-leave-active {
  transition: 0s;
}
.switch-enter,
.switch-leave-to {
  opacity: 0;
}
.switch-enter-to,
.switch-leave {
  opacity: 1;
}
.show-enter-active,
.show-leave-active {
  transition: opacity .4s;
}
.show-enter,
.show-leave-to {
  opacity: 0;
}
.slide-enter-active,
.slide-leave-active {
  transition: max-height 0.4s;
  overflow: hidden;
}
.slide-enter,
.slide-leave-to {
  max-height: 0;
}
.slide-enter-to,
.slide-leave {
  max-height: 30rem;
}
.slide-fade-enter-active,
.slide-fade-leave-active {
  transition: max-width 0.6s, max-height 0.4s, opacity 0.2s;
  overflow: hidden;
}
.slide-fade-enter,
.slide-fade-leave-to {
  max-width: 0;
  max-height: 0;
  opacity: 0;
  visibility: hidden;
}
.slide-fade-enter-to,
.slide-fade-leave {
  max-width: 30rem;
  max-height: 30rem;
  opacity: 1;
  visibility: visible;
}



