/* Loader */

.block-ui {
  display: none;
}

.block-ui .wrapper {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  text-align: center;
  vertical-align: middle;
}

.block-ui .cssload-loader {
  position: absolute;
  display: inline-block;
  z-index: 1000;
  top: 40%;
  left: 0;
  right: 0;
  margin: auto;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background-color: #357;
}

.block-ui .cssload-loader,
.block-ui .cssload-loader:before,
.block-ui .cssload-loader:after {
  animation: 0.5s infinite ease-in-out;
  -o-animation: 0.5s infinite ease-in-out;
  -ms-animation: 0.5s infinite ease-in-out;
  -webkit-animation: 0.5s infinite ease-in-out;
  -moz-animation: 0.5s infinite ease-in-out;
}

.block-ui .cssload-loader:before,
.block-ui .cssload-loader:after {
  width: 100%; 
  height: 100%;
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 0;
}

.block-ui .cssload-loader { animation-name: cssload-loader;
  -o-animation-name: cssload-loader;
  -ms-animation-name: cssload-loader;
  -webkit-animation-name: cssload-loader;
  -moz-animation-name: cssload-loader; }


@keyframes cssload-loader {
  from { transform: scale(0); opacity: 1; }
  to   { transform: scale(1); opacity: 0; }
}

@-o-keyframes cssload-loader {
  from { -o-transform: scale(0); opacity: 1; }
  to   { -o-transform: scale(1); opacity: 0; }
}

@-ms-keyframes cssload-loader {
  from { -ms-transform: scale(0); opacity: 1; }
  to   { -ms-transform: scale(1); opacity: 0; }
}

@-webkit-keyframes cssload-loader {
  from { -webkit-transform: scale(0); opacity: 1; }
  to   { -webkit-transform: scale(1); opacity: 0; }
}

@-moz-keyframes cssload-loader {
  from { -moz-transform: scale(0); opacity: 1; }
  to   { -moz-transform: scale(1); opacity: 0; }
}

