  line-height: 3rem;
  overflow-x: hidden;
  border: 1px solid #999;
  border-radius: 2px;
  vertical-align: middle;
  text-align: center;
  padding: 0 1rem;
  cursor: pointer;
}

.file-hidden {
  height: 0;
  width: 0;
  overflow: hidden;
}

/* Autocomplete */

.autocomplete-container {
  position: relative;
}

.autocomplete-suggestions {
  margin-top: 0.5rem;
  border: 1px solid #999;
  border-radius: 2px;
  background-color: #ffffff;
  box-shadow: 0 5px 11px 0 rgba(0, 0, 0, 0.18), 0 4px 15px 0 rgba(0, 0, 0, 0.15);
  border: none;
  border-collapse: separate;
  border-spacing: 1px;
  background-color: #fff; 
  overflow: auto;
  font-size: 1rem;
}

.autocomplete-suggestion {
  padding: 0.5rem 1rem; 
  white-space: nowrap; 
  overflow: hidden; 
  cursor: pointer; 
}

.autocomplete-selected {
  background-color: #f0f0f0;
}

.autocomplete-suggestions strong {
  font-weight: normal; 
  color: red;
}

.autocomplete-group {
   padding: 0.25rem 0.5rem; 
}

.autocomplete-group strong {
  display: block; 
  border-bottom: 1px solid #000;
}

/* Other */

.right {
  float: right;
}

span.error {
  display: none;
  margin-left: 0.5rem;
  border: none;
  border-radius: 2px;
  padding: 1px 0.5rem;
  background-color: orangered;
  color: white;
}

.red {
  color: orangered;
}

/* Calendar */

.vdp-datepicker {
  display: inline-block;
}

.vdp-datepicker .today {
  background-color: #ccc;
}

.vdp-datepicker__calendar {
  box-shadow: 0 3px 7px 0 rgba(0, 0, 0, 0.18), 0 2px 11px 0 rgba(0, 0, 0, 0.15);
  border: none;
  white-space: normal;
}


